import axios from 'axios'
import utils from './utils'

// const hosturl = '/apis/interface';
const hosturl = 'http://117.48.157.6/interface';
const hostToken = '/system/loginOnline';
const tokenMP = { 'username': 'wzxsjShow', 'password': '123', 'captcha': '', 'uuid': '' }

export default {
    post(urlElement, data) {
        return new Promise((resolve, reject) => {
            combineURL(urlElement, data).then(url => {
                axios.post(url).then(res => {
                    if (dealWithData(res)) {
                        resolve(res.data);
                    } else {
                        console.warn(`数据返回结果 ${res.data.msg}`);
                        if (res.data.msg == 'token失效，请重新登录') {
                            utils.removeCookie("token");
                        }
                        resolve(res.data.msg);
                    }
                }).catch(err => {
                    console.error(`服务器访问错误`);
                    reject(err);
                })
            });
        })
    },
    get(urlElement, data) {
        return new Promise((resolve, reject) => {
            combineURL(urlElement, data).then(url => {
                axios.get(url).then(res => {
                    if (dealWithData(res)) {
                        resolve(res.data);
                    } else {
                        console.warn(`数据返回结果 ${res.data.msg}`);
                        if (res.data.msg == 'token失效，请重新登录') {
                            utils.removeCookie("token");
                        }
                        resolve('');
                    }
                }).catch(err => {
                    console.error(`服务器访问错误`);
                    reject(err);
                })
            });
        })
    },
}

// 获取token
function getToken() {
    return new Promise((resolve, reject) => {
        let url = hosturl + hostToken;
        let data = tokenMP;
        axios.post(url, data).then(res => {

            if (dealWithData(res)) {
                let extimes = res.data.expire; // 保持时间（小时）
                utils.setCookie('token', res.data.token, extimes);
                resolve(res.data.token);
            } else {
                console.warn(`数据返回结果 ${res.data.msg}`);
                resolve('');
            }
        }).catch(err => {
            console.error(`服务器访问错误，Token获取失败`);
            reject(err);
        })
    })
}

// 拼接字符串
function combineURL(urlElement, data) {
    return new Promise((resolve) => {
        let url = hosturl + urlElement + '?token=';
        let token = utils.getCookie('token');
        if (!token) {
            getToken().then(token => {
                url += token;
                for (let key in data) {
                    url += '&' + key + '=' + data[key]
                }
                resolve(url);
            })
        } else {
            url += token;
            for (let key in data) {
                url += '&' + key + '=' + data[key]
            }
            resolve(url);
        }
    })
}

// 处理返回值
function dealWithData(data) {
    if (data.status != 200 || !data.data || data.data.code != 0) {
        return false
    }
    return true
}


