<template>
  <div class="shoes_page">
    <div class="pages_top">
      <div class="top_clock">
        <div id="clock"></div>
      </div>
      <div class="top_title">
        <div>{{titleData.titieName}}</div>
        <div>{{titleData.titieTime}}</div>
      </div>
      <!-- <div class="top_number">
        <div>
          <p>当前订货量：</p>
          <p>1212</p>
        </div>
        <div>
          <p>当前订单量：</p>
          <p>1212</p>
        </div>
      </div>-->
    </div>
    <div class="pages_center">
      <div class="center_title">
        <div>
          <div id="pageName">订货量排行榜</div>
        </div>
        <div>男女鞋占比</div>
        <div>男女品类占比</div>
      </div>
      <div class="center_chart">
        <div class="swiper_box" id="swiper_box">
          <div id="page_OrderQuantity" class="every_box">
            <div class="Columnar_charts">
              <shoes-charts
                :chartsId="'bar1'"
                :chartsType="'bar'"
                :chartsHeight="pageHeight"
                :chartsData="chartsData_bar1"
              ></shoes-charts>
            </div>
            <div class="pie_charts1">
              <shoes-charts
                :chartsId="'pie1'"
                :chartsType="'pie'"
                :chartsHeight="pageHeight"
                :chartsData="chartsData_pie1"
              ></shoes-charts>
            </div>
            <div class="pie_charts2">
              <!-- <shoes-charts :chartsId="'pieChart2'" :chartsType="'pie2'" :pageHeight="pageHeight"></shoes-charts> -->
            </div>
            <div class="charts1_datas">
              <div>
                <div>关注度：</div>
                <div>12000</div>
              </div>
              <div>
                <div>
                  <p></p>
                  <p>男鞋：</p>
                </div>
                <div>60%</div>
                <div>18000</div>
              </div>
              <div>
                <div>
                  <p></p>
                  <p>女鞋：</p>
                </div>
                <div>40%</div>
                <div>14000</div>
              </div>
            </div>
          </div>
          <div id="page_Concern" class="every_box">
            <div class="Columnar_charts">
              <shoes-charts
                :chartsId="'bar2'"
                :chartsType="'bar2'"
                :chartsHeight="pageHeight"
                :chartsData="chartsData_bar1"
              ></shoes-charts>
            </div>
            <div class="pie_charts1">
              <!-- <shoes-charts :chartsId="'pieChart3'" :chartsType="'pie'" :pageHeight="pageHeight"></shoes-charts> -->
            </div>
            <div class="pie_charts2">
              <!-- <shoes-charts :chartsId="'pieChart4'" :chartsType="'pie2'" :pageHeight="pageHeight"></shoes-charts> -->
            </div>
            <div class="charts1_datas">
              <div>
                <div>关注度：</div>
                <div>12000</div>
              </div>
              <div>
                <div>
                  <p></p>
                  <p>男鞋：</p>
                </div>
                <div>60%</div>
                <div>18000</div>
              </div>
              <div>
                <div>
                  <p></p>
                  <p>女鞋：</p>
                </div>
                <div>40%</div>
                <div>14000</div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="center_pageTurn">
        <div :class="nowBox==0?'chooseBox':''" @click="changeBox(0)"></div>
        <div :class="nowBox==1?'chooseBox':''" @click="changeBox(1)"></div>
      </div>
    </div>
    <div class="pages_bottom">
      <div>
        单
        <br>品
        <br>排
        <br>行
      </div>
      <div>
        <div v-for="(item, index) in bottom_six" :key="index">
          <div>
            <img :src="item.imgSrc" alt>
          </div>
          <div>
            <div>
              <img :src="item.wxORCode" alt>
            </div>
            <div>{{item.shoesSeq}}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import shoesCharts from "../components/shoesCharts";
import axios from "../axios/axios";
import utils from "../axios/utils";

var pageInterface = "/system/onlineRank/rankList";
export default {
  data() {
    return {
      nowBox: 0,
      pageHeight: 0,
      //标题数据
      titleData: {},
      // 图表数据
      chartsData_bar1: {},
      chartsData_pie1: {},
      chartsData_pie2: {},
      // 底部数据
      bottom_six: []
    };
  },
  components: {
    shoesCharts
  },
  methods: {
    /** 获取当前网页可用高度 start */
    GetPageHeight() {
      // 获取网页可视页面的高度
      let pageH = document.documentElement.clientHeight;
      // console.log("网页可见高：" + pageH);
      this.pageHeight = pageH;
      // 设置高度
      $(".shoes_page").css("height", pageH + "px");
    },
    /** 获取当前网页可用高度 end */

    /** 点击切换charts的BOX start */
    changeBox(index) {
      // 定义一个变量记住上一次的位置
      let old = 0;
      old = this.nowBox; //此时的pageSwiperNow还是上一次的index值
      // 现在所处位置
      this.nowBox = index; //此时的pageSwiperNow是最新的index值
      if (old != this.nowBox) {
        $("#swiper_box").css("left", index * -100 + "%");
      }

      this.changeElementName(index);
    },

    changeElementName(index) {
      // 页面切换更改页面的
      if (index == 0) {
        $("#pageName").text("订货量排行榜");

        this.GetPageDatas(2, "");
      } else {
        $("#pageName").text("关注度排行榜");

        this.GetPageDatas(1, "");
      }
    },
    /** 点击切换charts的BOX end */

    /** swiper动画 start */
    swiperBox1() {
      this.changeBox(1);
    },
    swiperBox2() {
      this.changeBox(0);
    },
    /** swiper动画 end */

    /** 获取页面的数据 start */
    GetPageDatas(type, categorySeq) {
      axios.get(pageInterface, { type: type }).then(res => {
        console.log(res.rank);
        if (res.code == 0) {
          // 标题数据
          let topData = {};
          topData.titieName = res.rank.goodsPeriodEntity.name;
          let start_time = res.rank.goodsPeriodEntity.meetingStartTime
            .split(" ")[0]
            .split("/");
          let end_time = res.rank.goodsPeriodEntity.meetingEndTime
            .split(" ")[0]
            .split("/");
          topData.titieTime =
            start_time[0] +
            "." +
            start_time[1] +
            "." +
            start_time[2] +
            "-" +
            end_time[0] +
            "." +
            end_time[1] +
            "." +
            end_time[2];
          this.titleData = topData;

          // 柱状图 图表数据
          let barData = [];
          let bottom_six = [];
          if (res.rank.rankList.length > 30) {
            barData = res.rank.rankList.slice(-30);
          } else {
            barData = res.rank.rankList;
          }
          bottom_six = res.rank.rankList.slice(-6);
          this.bottom_six = bottom_six;

          let chartsX = [];
          let chartsY = [];
          let chartsY2 = [];
          for (let i in barData) {
            chartsX.unshift(barData[i].shoesSeq + "");
            chartsY.unshift(barData[i].num);
            chartsY2.unshift(barData[i].count);
          }

          let bar1Datas = {};
          bar1Datas.chartsX = chartsX;
          bar1Datas.chartsY = chartsY;
          bar1Datas.chartsY2 = chartsY2;
          this.chartsData_bar1 = bar1Datas;

          // 第一个饼图数据
          let pieDatas = [];
          for (let i in res.rank.goodsCategoryList) {
            let obj = {
              itemStyle: {
                borderWidth: 5
              }
            };

            obj.value = res.rank.goodsCategoryList[i].num;
            if (i == 0) {
              obj.selected = false;
              obj.itemStyle.borderColor = "rgba(51, 111, 255, 1)";
            } else {
              obj.selected = true;
              obj.itemStyle.borderColor = "rgba(255, 76, 78, 1)";
            }
            pieDatas.push(obj);
          }

          console.log(pieDatas);
          let obj = {};
          obj.pieDatas = pieDatas;
          this.chartsData_pie1 = obj;
        }
      });
    },
    /** 获取页面的数据 end */

    /** 默认加载页面数据 */
    defaultLoad() {
      this.GetPageDatas(2, "");
    }
  },
  mounted() {
    // 监听网页窗口的改变动态改变窗口的高度
    let that = this;
    $(window).resize(function() {
      that.GetPageHeight();
      // 页面改变大小的时候重新刷新一下页面
      // this.$router.replace({ path: '/shoes', query: {} });
      location.reload();
    });

    // let swiper1 = setInterval(this.swiperBox1, 3000);
    // let swiper2 = setInterval(this.swiperBox2, 6000);

    this.GetPageHeight();
    this.defaultLoad();
  }
};

/** 页面头的时间 start */
function timeClock() {
  let clock = new Date();
  let year = clock.getFullYear();
  let month = clock.getMonth() + 1;
  let day = clock.getDate();
  let week = clock.getDay();
  let hour = clock.getHours();
  let minutes = clock.getMinutes();
  let seconds = clock.getSeconds();
  let monthNew = "";
  let dayNew = "";
  let weekNew = "";
  let hourNew = "";
  let minutesNew = "";
  let secondsNew = "";

  if (month < 10) {
    monthNew = "0" + month;
  } else {
    monthNew = month;
  }
  if (day < 10) {
    dayNew = "0" + day;
  } else {
    dayNew = day;
  }
  if (hour < 10) {
    hourNew = "0" + hour;
  } else {
    hourNew = hour;
  }
  if (minutes < 10) {
    minutesNew = "0" + minutes;
  } else {
    minutesNew = minutes;
  }
  if (seconds < 10) {
    secondsNew = "0" + seconds;
  } else {
    secondsNew = seconds;
  }
  let nowTime1 =
    year +
    "年" +
    monthNew +
    "月" +
    dayNew +
    "日" +
    "  " +
    hourNew +
    ":" +
    minutesNew +
    ":" +
    secondsNew;

  $("#clock").text(nowTime1);
}
/** 页面头的时间 end */
setInterval(timeClock, 1000);
</script>

<style lang="less" scoped>
.shoes_page {
  // border: 1px solid red;
  width: 100%;
  background: url("../../static/images/Bitmap.png");
  background-size: 100% 100%;
  color: white;
  > .pages_top {
    // border: 1px solid red;
    width: 100%;
    height: 9.5%;
    display: flex;
    flex-flow: row nowrap;
    > div {
      // border: 1px solid red;
      width: 33.33%;
      height: 100%;
      display: flex;
    }
    > .top_clock {
      justify-content: flex-start;
      align-items: center;
      padding-left: 2%;
      > div {
        font-size: 30px;
        font-weight: bold;
        letter-spacing: 2px;
      }
    }
    > .top_title {
      flex-flow: column nowrap;
      > div {
        // border: 1px solid red;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
      }
      > div:nth-child(1) {
        height: 60%;
        font-size: 40px;
        font-weight: 300px;
        letter-spacing: 3px;
      }
      > div:nth-child(2) {
        height: 40%;
        position: relative;
        top: 18%;
        font-size: 22px;
        font-weight: bold;
      }
    }
    > .top_number {
      justify-content: flex-end;
      align-items: flex-end;
      > div {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        margin-left: 30px;
        padding-right: 7%;
        > p:nth-child(1) {
          font-size: 18px;
          letter-spacing: 1px;
        }
        > p:nth-child(2) {
          font-size: 35px;
          font-weight: bold;
          letter-spacing: 1px;
        }
      }
    }
  }
  > .pages_center {
    // border: 1px solid red;
    width: 100%;
    height: 72%;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    > div {
      width: 97%;
      display: flex;
    }
    > .center_title {
      height: 10%;
      > div:nth-child(1) {
        // border: 1px solid red;
        width: 60%;
        padding-left: 4%;
        > div {
          height: 80%;
          display: flex;
          align-items: flex-end;
          font-size: 28px;
          font-weight: bold;
          letter-spacing: 11px;
          padding-bottom: 7px;
        }
      }
      > div:nth-child(2) {
        // border: 1px solid red;
        width: 20%;
        display: flex;
        align-items: flex-end;
        font-size: 20px;
        letter-spacing: 6px;
        padding-bottom: 7px;
      }
      > div:nth-child(3) {
        // border: 1px solid red;
        width: 20%;
        display: flex;
        align-items: flex-end;
        font-size: 20px;
        letter-spacing: 5px;
        padding-bottom: 7px;
      }
    }
    > .center_chart {
      // border: 1px solid red;
      width: 94.7%;
      height: 87.5%;
      position: relative;
      overflow: hidden;
      > .swiper_box {
        // border: 1px solid yellow;
        display: flex;
        width: 200%;
        // width: 300%;
        height: 100%;
        position: absolute;
        left: 0%;
        // transition: left 0.4s;
        > .every_box {
          // border: 1px solid red;
          width: 100%;
          position: relative;
          > .Columnar_charts {
            // border: 1px solid pink;
            width: 100%;
            height: 100%;
          }
          > .pie_charts1 {
            // border: 1px solid pink;
            width: 20%;
            height: 20%;
            position: absolute;
            top: 0%;
            right: 20%;
          }
          > .pie_charts2 {
            // border: 1px solid pink;
            width: 20%;
            height: 35%;
            position: absolute;
            top: 0%;
            right: 0%;
          }
          > .charts1_datas {
            // border: 1px solid pink;
            width: 12%;
            height: 20%;
            position: absolute;
            top: 0%;
            right: 20%;
            display: flex;
            flex-flow: column nowrap;
            justify-content: center;
            padding-right: 10px;
            color: rgb(150, 150, 153);
            > div {
              display: flex;
              justify-content: flex-end;
              margin: 5px 0px;
            }
            > div:nth-child(1) {
              justify-content: flex-start;
            }
            > div:nth-child(2),
            > div:nth-child(3) {
              > div {
                margin: 0px 5px;
              }
              > div:nth-child(1) {
                width: 35%;
                display: flex;
                justify-content: space-between;
                align-items: center;
                > p:nth-child(1) {
                  width: 14%;
                  height: 40%;
                  border-radius: 100%;
                  background: rgba(51, 111, 255, 1);
                }
              }
            }
            > div:nth-child(3) {
              > div:nth-child(1) {
                > p:nth-child(1) {
                  background: rgba(255, 76, 78, 1);
                }
              }
            }
          }
        }
      }
    }
    > .center_pageTurn {
      // border: 1px solid red;
      height: 2.5%;
      display: flex;
      flex-flow: row nowrap;
      justify-content: center;
      align-items: center;
      > div {
        width: 30px;
        height: 8px;
        margin: 0px 10px;
        border-radius: 4px;
        background: rgba(148, 146, 146, 0.664);
        cursor: pointer;
      }
      .chooseBox {
        background: #2969ff;
      }
    }
  }
  > .pages_bottom {
    // border: 1px solid red;
    width: 100%;
    height: 18%;
    display: flex;
    > div:nth-child(1) {
      width: 5.4%;
      font-size: 18px;
      display: flex;
      justify-content: center;
      align-items: center;
      padding-left: 0.3%;
      padding-top: 0.6%;
    }
    > div:nth-child(2) {
      width: 94.6%;
      display: flex;
      justify-content: space-around;
      align-items: center;
      > div {
        // border: 1px solid blue;
        width: 15%;
        height: 80%;
        border-radius: 7px;
        display: flex;
        flex-flow: row nowrap;
        padding: 10px;
        background: rgb(240, 238, 238);
        > div:nth-child(1) {
          // border: 1px solid black;
          width: 60%;
          padding: 2px;
          > img {
            width: 100%;
            height: 100%;
          }
        }
        > div:nth-child(2) {
          // border: 1px solid pink;
          width: 40%;
          display: flex;
          flex-flow: column nowrap;
          > div:nth-child(1) {
            // border: 1px solid black;
            height: 75%;
            padding: 2px;
            > img {
              width: 100%;
              height: 100%;
            }
          }
          > div:nth-child(2) {
            // border: 1px solid pink;
            height: 25%;
            color: black;
            font-weight: bold;
            display: flex;
            justify-content: center;
            align-items: center;
          }
        }
      }
    }
  }
}
</style>



