<template>
  <div class="chartsDiv" :id="chartsId" :style="{width: '100%', height: '100%'}"></div>
</template>

<script>
var id = null;
var type = null;
var height = null;
var XData = null;
var YData = null;
var YData2 = null;

import $ from "jquery";
export default {
  data() {
    return {};
  },
  props: {
    chartsId: {
      type: String,
      default: ""
    },
    chartsType: {
      type: String,
      default: ""
    },
    chartsHeight: {
      type: Number,
      default: 1080
    },
    chartsData: {
      type: Object,
      default: null
    }
  },
  watch: {
    chartsData(newV, oldV) {
      // console.log(newV);
      this.drawCharts(newV);
    }
  },
  methods: {
    init() {
      id = null;
      type = null;
      height = null;
      XData = null;
      YData = null;
      YData2 = null;
    },
    drawCharts(chartsData) {
      this.init();

      id = this.chartsId;
      type = this.chartsType;
      height = this.chartsHeight;

      if (type == "bar") {
        /**
         * 柱状图
         * */
        // 基于准备好的dom，初始化echarts实例
        $(".chartsDiv").css("height", height * 0.72 * 0.875);
        XData = chartsData.chartsX;
        YData = chartsData.chartsY;
        YData2 = chartsData.chartsY2;

        let myChart = this.$echarts.init(document.getElementById(id));
        // 开始绘制
        myChart.setOption({
          grid: {
            top: "15%",
            right: "0%",
            bottom: "5%",
            left: "0%",
            containLabel: true
          },
          xAxis: [
            {
              type: "category",
              data: XData,
              axisPointer: {
                type: "shadow"
              },
              axisLine: {
                show: false,
                lineStyle: {
                  color: "#ccc"
                }
              },
              axisTick: {
                show: false
              }
            }
          ],
          yAxis: [
            {
              type: "value",
              name: "数量",
              min: 0,
              show: false,
              axisTick: {
                show: false
              }
            },
            {
              type: "value",
              name: "次数",
              min: 0,
              interval: 10,
              show: false,
              axisTick: {
                show: false
              }
            }
          ],
          series: [
            {
              name: "数量",
              type: "bar",
              itemStyle: {
                normal: {
                  //颜色渐变
                  color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                    { offset: 0, color: "rgba(247, 8, 111, 1)" },
                    { offset: 1, color: "rgba(227, 157, 195, 0.02)" }
                  ])
                }
              },
              data: YData,
              barMaxWidth: "30"
            },
            {
              name: "次数",
              type: "line",
              yAxisIndex: 1,
              symbolSize: 8,
              lineStyle: {
                color: "#F1770C"
              },
              data: YData2
            }
          ],
          textStyle: {
            color: "white"
          }
        });
      } else if (type == "bar2") {
        /**
         * 柱状图
         * */
        $(".chartsDiv").css("height", height * 0.72 * 0.875);
        XData = chartsData.chartsX;
        YData2 = chartsData.chartsY2;
        
        // 基于准备好的dom，初始化echarts实例
        let myChart = this.$echarts.init(document.getElementById(id));
        // 开始绘制
        myChart.setOption({
          grid: {
            top: "15%",
            right: "1.5%",
            bottom: "5%",
            left: "0%",
            containLabel: true
          },
          xAxis: [
            {
              type: "category",
              data: XData,
              axisPointer: {
                type: "shadow"
              },
              axisLine: {
                show: false,
                lineStyle: {
                  color: "#ccc"
                }
              },
              axisTick: {
                show: false
              }
            }
          ],
          yAxis: [
            {
              type: "value",
              name: "数量",
              min: 0,
              show: false,
              axisTick: {
                show: false
              }
            }
          ],
          series: [
            {
              name: "数量",
              type: "bar",
              itemStyle: {
                normal: {
                  //颜色渐变
                  color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                    { offset: 0, color: "rgba(247, 120, 8, 1)" },
                    { offset: 1, color: "rgba(227, 199, 157, 0.02)" }
                  ])
                }
              },
              data: YData2,
              barMaxWidth: "30"
            }
          ],
          textStyle: {
            color: "white"
          }
        });
      } else if (type == "pie") {
        /**
         * 饼图
         * */
        // 基于准备好的dom，初始化echarts实例
        $(".chartsDiv").css("height", height * 0.72 * 0.875 * 0.2);
        let PieChart = this.$echarts.init(document.getElementById(id));
        // 开始绘制
        PieChart.setOption({
          series: [
            {
              type: "pie",
              radius: "76%",
              center: ["20%", "50%"],
              hoverAnimation: true,
              // selectedOffset: 5,
              data: [
                {
                  value: 10,
                  selected: true,
                  itemStyle: {
                    borderColor: "rgba(51, 111, 255, 1)",
                    borderWidth: 5
                  }
                },
                {
                  value: 20,
                  selected: true,
                  temStyle: {
                    borderColor: "rgba(255, 76, 78, 1)",
                    borderWidth: 5
                  }
                }
              ],
              labelLine: {
                show: false //控制饼图的硬导线
              },
              itemStyle: {
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: "rgba(0, 0, 0, 0.5)"
                }
              }
            }
          ],
          color: ["rgba(51, 111, 255, 1)", "rgba(255, 76, 78, 1)"]
        });
      } else if (type == "pie2") {
        /**
         * 饼图
         * */
        $(".chartsDiv").css("height", height * 0.72 * 0.875 * 0.35);
        // 基于准备好的dom，初始化echarts实例
        let PieChart = this.$echarts.init(document.getElementById(id));
        // 开始绘制
        PieChart.setOption({
          tooltip: {
            trigger: "item",
            formatter: "{b} : {c} ({d}%)"
          },
          series: [
            {
              type: "pie",
              radius: "50%",
              // center: ["50%", "50%"],
              data: [
                { value: 1003, name: "凉鞋" },
                { value: 2000, name: "皮鞋" },
                { value: 1727, name: "休闲鞋" },
                { value: 1397, name: "运动鞋" }
              ],
              label: {
                formatter: "{b|{b}}:\n\n{d|{d}%} {c|{c}}",
                rich: {
                  b: {
                    fontSize: 13
                  },
                  d: {
                    fontSize: 13,
                    fontWeight: "bold"
                  },
                  c: {
                    fontSize: 15,
                    fontWeight: "bold"
                  }
                }
              },
              labelLine: {
                show: true //控制饼图的硬导线
              },
              itemStyle: {
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: "rgba(0, 0, 0, 0.5)"
                }
              }
            }
          ]
        });
      }
    }
  },
  mounted() {
    // this.drawCharts(
    //   this.$props.chartsId,
    //   this.$props.chartsType,
    //   this.$props.pageHeight1
    // );
  }
};
</script>

<style lang="less" scoped>
.chartsDiv {
  background: rgba(255, 255, 255, 0);
}
</style>



