import Vue from 'vue'
import Router from 'vue-router'
import shoesPage from '../pages/shoesPage'

Vue.use(Router)

export default new Router({
  routes: [
    { path: '/', redirect: "/shoes" },
    { path: '/shoes', name: 'shoes', component: shoesPage }
  ]
})



